.App {
  position: absolute;
  height: 100%;
  width: 100%;
}

.geoLocate {
	position: absolute;
	-webkit-transform: translateX(-50%);
	    -ms-transform: translateX(-50%);
	        transform: translateX(-50%);
	left: 50%;
	width: 300px;
	height: 100px;
	background: rgba(100, 100, 100, .5);
	z-index: 1;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}

.geoLocate button {
	padding: 20px 50px;
	background: rgb(20, 180, 200);
	border: none;
	color: white;
	cursor: pointer;
	font-size: 16px;
}


